.analytics-section .analytics-grid{display:grid;grid-template-columns:repeat(auto-fit, minmax(300px, 1fr));gap:1.5rem}.analytics-section .analytics-grid .analytics-card{padding:1.5rem}.analytics-section .analytics-grid .analytics-card.wide{grid-column:1/-1}.analytics-section .analytics-grid .analytics-card h3{margin:0 0 1rem 0;font-size:1.1rem;color:#333;border-bottom:2px solid #f0f0f0;padding-bottom:.5rem}.group-stats .group-stat{display:flex;align-items:center;gap:1rem;margin-bottom:1rem}.group-stats .group-stat:last-child{margin-bottom:0}.group-stats .group-stat .group-name{flex:0 0 80px;font-weight:500;color:#333}.group-stats .group-stat .stat-bar{flex:1;height:8px;background:#e9ecef;border-radius:4px;overflow:hidden}.group-stats .group-stat .stat-bar .bar-fill{height:100%;background:linear-gradient(90deg, #2c5aa0, #4a76a8);border-radius:4px;transition:width .3s ease}.group-stats .group-stat .stat-value{flex:0 0 60px;text-align:right;font-size:.9rem;color:#666;font-weight:500}.average-grades .grade-item{display:flex;justify-content:space-between;align-items:center;padding:.75rem 0;border-bottom:1px solid #f0f0f0}.average-grades .grade-item:last-child{border-bottom:none}.average-grades .grade-item .grade-value{font-weight:600;color:#2c5aa0;font-size:1.1rem}.average-grades .grade-item .grade-value::after{content:" ";font-size:.8rem;color:#666;font-weight:normal;margin-left:2px}.activity-chart .chart-header{display:flex;justify-content:space-between;align-items:center;margin-bottom:1.5rem}.activity-chart .chart-header .chart-legend{display:flex;gap:1rem}.activity-chart .chart-header .chart-legend .legend-item{display:flex;align-items:center;gap:.5rem;font-size:.9rem;color:#666}.activity-chart .chart-header .chart-legend .legend-item .legend-color{width:12px;height:12px;border-radius:2px}.activity-chart .chart-header .chart-legend .legend-item .legend-color.submitted{background:#4a76a8}.activity-chart .chart-header .chart-legend .legend-item .legend-color.graded{background:#28a745}.activity-chart .chart-header .time-period select{padding:.5rem;border:1px solid #e1e5e9;border-radius:4px;font-size:.9rem;background:#fff}.activity-chart .chart-header .time-period select:focus{outline:none;border-color:#2c5aa0}.activity-chart .chart-placeholder .chart-mock .chart-bars{display:flex;align-items:flex-end;justify-content:space-between;height:150px;margin-bottom:1rem;padding:0 1rem}.activity-chart .chart-placeholder .chart-mock .chart-bars .chart-bar{display:flex;flex-direction:column;align-items:center;height:100%;width:30px;gap:2px}.activity-chart .chart-placeholder .chart-mock .chart-bars .chart-bar .bar{width:12px;border-radius:2px 2px 0 0;transition:height .3s ease}.activity-chart .chart-placeholder .chart-mock .chart-bars .chart-bar .bar.submitted{background:#4a76a8}.activity-chart .chart-placeholder .chart-mock .chart-bars .chart-bar .bar.graded{background:#28a745}.activity-chart .chart-placeholder .chart-mock .chart-labels{display:flex;justify-content:space-between;padding:0 1rem}.activity-chart .chart-placeholder .chart-mock .chart-labels .chart-label{font-size:.8rem;color:#666;width:30px;text-align:center}@media(max-width: 768px){.analytics-section .analytics-grid{grid-template-columns:1fr}.activity-chart .chart-header{flex-direction:column;gap:1rem;align-items:stretch}}