.footer{background:linear-gradient(135deg, #2c3e50 0%, #34495e 100%);color:#ecf0f1;margin-top:auto;border-top:3px solid #3498db;position:relative;overflow:hidden}.footer::before{content:"";position:absolute;top:0;left:0;right:0;height:1px;background:linear-gradient(90deg, transparent, #3498db, transparent)}.footer .footer-content{display:grid;grid-template-columns:repeat(auto-fit, minmax(250px, 1fr));gap:2rem;padding:3rem 2rem;max-width:1200px;margin:0 auto;position:relative;z-index:1}@media(max-width: 768px){.footer .footer-content{grid-template-columns:1fr;gap:1.5rem;padding:2rem 1rem}}.footer .footer-section{opacity:0;transform:translateY(30px);animation:fadeInUp .8s ease forwards}.footer .footer-section:nth-child(1){animation-delay:.1s}.footer .footer-section:nth-child(2){animation-delay:.2s}.footer .footer-section:nth-child(3){animation-delay:.3s}.footer .footer-section:nth-child(4){animation-delay:.4s}.footer .footer-section h4{color:#3498db;margin-bottom:1rem;font-size:1.2rem;font-weight:600;position:relative}.footer .footer-section h4::after{content:"";position:absolute;bottom:-5px;left:0;width:40px;height:2px;background:#3498db;transition:width .3s ease}.footer .footer-section h4:hover::after{width:60px}.footer .footer-section h5{color:#bdc3c7;margin-bottom:1rem;font-size:1rem;font-weight:500;transition:color .3s ease}.footer .footer-section h5:hover{color:#3498db}.footer .footer-section p{margin:.5rem 0;line-height:1.5;color:#bdc3c7;transition:color .3s ease}.footer .footer-contacts p{display:flex;align-items:center;gap:.5rem;margin:.75rem 0;transition:all .3s ease}.footer .footer-contacts p:hover{color:#3498db;transform:translateX(5px)}.footer .footer-links{list-style:none;padding:0;margin:0}.footer .footer-links li{margin:.5rem 0;position:relative}.footer .footer-links li::before{content:"";position:absolute;left:-15px;color:#3498db;opacity:0;transition:all .3s ease}.footer .footer-links li a{color:#bdc3c7;text-decoration:none;transition:all .3s ease;display:block;padding:.25rem 0;position:relative;overflow:hidden}.footer .footer-links li a::before{content:"";position:absolute;left:-100%;bottom:0;width:100%;height:1px;background:#3498db;transition:left .3s ease}.footer .footer-links li a:hover{color:#3498db;transform:translateX(8px)}.footer .footer-links li a:hover::before{left:0}.footer .footer-links li:hover::before{opacity:1;transform:translateX(-3px)}.footer .system-info p{display:flex;align-items:center;gap:.5rem;margin:.5rem 0;transition:all .3s ease}.footer .system-info p:hover{color:#ecf0f1;transform:translateX(3px)}.footer .system-info .status-active{color:#2ecc71;font-weight:500;position:relative;padding-left:15px}.footer .system-info .status-active::before{content:"";position:absolute;left:0;top:50%;transform:translateY(-50%)}.footer .footer-bottom{border-top:1px solid #4a6278;padding:1.5rem 2rem;display:flex;justify-content:space-between;align-items:center;flex-wrap:wrap;gap:1rem;max-width:1200px;margin:0 auto;position:relative;z-index:1;opacity:0;animation:fadeIn .8s ease .5s forwards}@media(max-width: 768px){.footer .footer-bottom{flex-direction:column;text-align:center;padding:1rem}}.footer .footer-bottom p{margin:0;color:#95a5a6;font-size:.9rem;transition:color .3s ease}.footer .footer-bottom p:hover{color:#bdc3c7}.footer .footer-developers p{font-style:italic;position:relative}.footer .footer-developers p::after{content:"";margin-left:5px;display:inline-block}@keyframes fadeInUp{from{opacity:0;transform:translateY(30px)}to{opacity:1;transform:translateY(0)}}@keyframes fadeIn{from{opacity:0}to{opacity:1}}@media(prefers-color-scheme: dark){.footer{background:linear-gradient(135deg, #1a1a1a 0%, #2d2d2d 100%)}.footer::before{background:linear-gradient(90deg, transparent, #2ecc71, transparent)}}@media(prefers-reduced-motion: reduce){.footer *{animation:none !important;transition:none !important}.footer-section{opacity:1;transform:none}.footer-bottom{opacity:1}}