.submissions-table-container {
  background: white;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  border: 1px solid #e5e7eb;
}

.table-header {
  padding: 1rem 1.5rem;
  background: #f8fafc;
  border-bottom: 1px solid #e5e7eb;
}

.table-info {
  display: flex;
  gap: 1.5rem;
  font-size: 0.875rem;
  color: #6b7280;

  strong {
    color: #374151;
  }
}

.table-scroll-container {
  width: 100%;
  overflow-x: auto;

  &::-webkit-scrollbar {
    height: 8px;
  }

  &::-webkit-scrollbar-track {
    background: #f1f5f9;
  }

  &::-webkit-scrollbar-thumb {
    background: #cbd5e1;
    border-radius: 4px;

    &:hover {
      background: #94a3b8;
    }
  }
}

.submissions-table {
  width: 100%;
  border-collapse: collapse;
  min-width: auto;

  th,
  td {
    padding: 0.875rem 1rem;
    text-align: left;
    border-bottom: 1px solid #f1f5f9;
    vertical-align: middle;
  }

  th {
    background: #f8fafc;
    font-weight: 600;
    color: #374151;
    font-size: 0.875rem;
    white-space: nowrap;
    position: sticky;
    top: 0;
    z-index: 10;
  }

  .student-column {
    min-width: 140px;
    max-width: 160px;
  }

  .assignment-column {
    min-width: 180px;
    max-width: 220px;
  }

  .group-column {
    min-width: 80px;
    max-width: 100px;
  }

  .date-column {
    min-width: 120px;
    max-width: 140px;
  }

  .file-column {
    min-width: 150px;
    max-width: 200px;
  }

  .status-column {
    min-width: 120px;
    max-width: 140px;
  }

  .score-column {
    min-width: 80px;
    max-width: 100px;
  }

  .actions-column {
    min-width: 140px;
    max-width: 160px;
  }
}

.sortable-header {
  cursor: pointer;
  transition: background-color 0.2s ease;
  user-select: none;

  &:hover {
    background: #f1f5f9;
  }

  &.active {
    background: #e0f2fe;
    color: #0369a1;
  }

  .header-content {
    display: flex;
    align-items: center;
    gap: 0.5rem;
  }

  .sort-indicator {
    font-weight: 700;
  }
}

.submission-row {
  transition: background-color 0.2s ease;

  &:hover {
    background: #f8fafc;
  }

  &:last-child {
    td {
      border-bottom: none;
    }
  }
}

.student-info {
  .student-name {
    font-weight: 600;
    color: #1f2937;
    font-size: 0.9rem;
    margin-bottom: 0.25rem;
  }

  .student-id {
    font-size: 0.75rem;
    color: #6b7280;
  }
}

.assignment-info {
  .assignment-title {
    font-weight: 500;
    color: #374151;
    font-size: 0.9rem;
    line-height: 1.3;
  }
}

.group-tag {
  background: #e0f2fe;
  color: #0369a1;
  padding: 0.25rem 0.5rem;
  border-radius: 8px;
  font-size: 0.75rem;
  font-weight: 600;
  white-space: nowrap;
}

.submit-date {
  font-size: 0.875rem;
  color: #374151;
  white-space: nowrap;
}

.file-info {
  .file-download-btn {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    background: none;
    border: none;
    padding: 0;
    cursor: pointer;
    text-align: left;
    width: 100%;

    &:hover {
      .file-name {
        color: #2563eb;
        text-decoration: underline;
      }
    }

    .file-icon {
      font-size: 1rem;
      flex-shrink: 0;
    }

    .file-name {
      font-size: 0.875rem;
      color: #374151;
      font-weight: 500;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      transition: color 0.2s ease;
    }
  }

  .file-size {
    font-size: 0.75rem;
    color: #6b7280;
    margin-top: 0.25rem;
  }
}

.status-badge {
  padding: 0.375rem 0.75rem;
  border-radius: 8px;
  font-size: 0.75rem;
  font-weight: 600;
  white-space: nowrap;
  display: inline-block;

  &--warning {
    background: #fffbeb;
    color: #d97706;
    border: 1px solid #fed7aa;
  }

  &--success {
    background: #f0fdf4;
    color: #16a34a;
    border: 1px solid #bbf7d0;
  }

  &--danger {
    background: #fef2f2;
    color: #dc2626;
    border: 1px solid #fecaca;
  }
}

.score-display {
  font-weight: 600;
  color: #16a34a;
  font-size: 0.9rem;
  white-space: nowrap;

  .score-separator {
    color: #9ca3af;
    font-weight: 400;
  }
}

.no-score {
  color: #9ca3af;
  font-style: italic;
  font-size: 0.875rem;
}

.action-buttons {
  display: flex;
  gap: 0.375rem;
  flex-wrap: nowrap;

  .action-btn {
    padding: 0.375rem 0.5rem;
    font-size: 0.75rem;
    white-space: nowrap;
    flex: 1;
    min-width: 0;

    &:has(.btn__icon) {
      padding: 0.375rem;

      .btn__text {
        display: none;
      }
    }
  }
}

.empty-state {
  text-align: center;
  padding: 3rem 2rem;

  .empty-icon {
    font-size: 3rem;
    margin-bottom: 1rem;
    opacity: 0.5;
  }

  h3 {
    color: #6b7280;
    margin: 0 0 0.5rem 0;
    font-size: 1.25rem;
  }

  p {
    color: #9ca3af;
    margin: 0;
  }
}

.table-skeleton {
  padding: 1rem;
}

.skeleton-row {
  display: grid;
  grid-template-columns: 140px 180px 80px 120px 150px 120px 80px 140px;
  gap: 1rem;
  margin-bottom: 0.875rem;
  padding: 0.875rem 1rem;
  background: white;
  border-radius: 8px;
  border: 1px solid #f1f5f9;
}

.skeleton-cell {
  .skeleton-line {
    height: 16px;
    background: linear-gradient(90deg, #f1f5f9 25%, #e2e8f0 50%, #f1f5f9 75%);
    background-size: 200% 100%;
    animation: loading 1.5s infinite;
    border-radius: 4px;
  }
}

@keyframes loading {
  0% {
    background-position: 200% 0;
  }

  100% {
    background-position: -200% 0;
  }
}

@media (max-width: 1200px) {
  .submissions-table {
    .student-column {
      min-width: 120px;
      max-width: 140px;
    }

    .assignment-column {
      min-width: 160px;
      max-width: 200px;
    }

    .file-column {
      min-width: 130px;
      max-width: 180px;
    }
  }
}

@media (max-width: 768px) {
  .table-header {
    padding: 0.875rem 1rem;
  }

  .table-info {
    flex-direction: column;
    gap: 0.5rem;
  }

  .section-header {
    .filters {
      flex-wrap: wrap;
    }
  }

  .submissions-table {

    th,
    td {
      padding: 0.75rem 0.5rem;
    }

    .student-column {
      min-width: 100px;
      max-width: 120px;
    }

    .assignment-column {
      min-width: 140px;
      max-width: 160px;
    }

    .group-column {
      min-width: 60px;
      max-width: 80px;
    }

    .date-column {
      min-width: 100px;
      max-width: 120px;
    }

    .file-column {
      min-width: 120px;
      max-width: 140px;
    }

    .status-column {
      min-width: 100px;
      max-width: 120px;
    }

    .score-column {
      min-width: 60px;
      max-width: 80px;
    }

    .actions-column {
      min-width: 120px;
      max-width: 140px;
    }
  }

  .action-buttons {
    flex-direction: column;
    gap: 0.25rem;

    .action-btn {
      min-width: auto;
      padding: 0.25rem 0.375rem;
      font-size: 0.7rem;
    }
  }

  .skeleton-row {
    grid-template-columns: 100px 140px 60px 100px 120px 100px 60px 120px;
    gap: 0.5rem;
  }
}