 rotate(0deg)}50%{transform:translateY(-20px) rotate(5deg)}}.container{max-width:1200px;margin:0 auto;padding:0 24px}.section-header{text-align:center;margin-bottom:80px}.section-header .section-title{font-size:48px;font-weight:800;color:#1a1a1a;margin-bottom:16px}.section-header .section-subtitle{font-size:18px;color:#666;line-height:1.6;max-width:600px;margin:0 auto}.features-section{padding:120px 0;background:#f8f9fa;position:relative}.features-section::before{content:"";position:absolute;top:0;left:0;width:100%;height:100px;background:linear-gradient(180deg, #667eea 0%, transparent 100%);opacity:.1}.features-grid{display:grid;grid-template-columns:repeat(2, 1fr);gap:30px;max-width:1000px;margin:0 auto}@media(max-width: 768px){.features-grid{grid-template-columns:1fr;gap:25px;max-width:500px}}.feature-card{background:#fff;padding:40px 30px;border-radius:20px;text-align:center;position:relative;overflow:hidden;transition:all .3s ease;box-shadow:0 8px 30px rgba(0,0,0,.1);border:1px solid #f0f0f0}.feature-card:hover{transform:translateY(-8px);box-shadow:0 15px 40px rgba(0,0,0,.15);border-color:#e1e5e9}.feature-icon-wrapper{position:relative;width:80px;height:80px;margin:0 auto 24px;transition:transform .3s ease}.feature-icon-bg{position:absolute;top:0;left:0;width:100%;height:100%;background:var(--gradient);border-radius:20px;opacity:.1;transition:opacity .3s ease}.feature-card:hover .feature-icon-bg{opacity:.15}.feature-icon{position:relative;font-size:32px;line-height:80px}.feature-title{font-size:20px;font-weight:700;color:#1a1a1a;margin-bottom:16px;line-height:1.3}.feature-description{color:#666;line-height:1.6;font-size:15px;margin:0}.feature-decoration{position:absolute;bottom:0;left:0;right:0;height:3px;background:var(--gradient);transform:scaleX(0.8);transition:transform .3s ease}.feature-card:hover .feature-decoration{transform:scaleX(1)}.roles-section{padding:120px 0;background:#fff}.roles-grid{display:grid;grid-template-columns:repeat(auto-fit, minmax(350